import PostArticle from "@/components/post/postArticle";
export default {
  data() {
    return {
      cities: ["广州", "上海", "北京"],
      totalList: {},
      articleList: [],
      // 默认在第一页
      pageIndex: 1,

      // 默认显示五条
      pageSize: 5,
      start: 0,
      value: "",
    };
  },
  components: {
    PostArticle,
  },

  created() {
    this.getList();
    // console.log(this.$route.query.city);
  },
  watch: {
    // 监听url变化
    $route() {
      this.getList();
    },
  },
  methods: {
    guangzhou(item) {
      this.value = item;
      this.$router.push(`/post?city=${item}`);
    },
    getList() {
      let params = {
        _start: this.start,
        _limit: this.pageSize,
      };
      if (this.$route.query.city) {
        params.city = this.$route.query.city;
      }

      this.$axios({
        url: "/posts",
        params,
      }).then((res) => {
        console.log(res.data.data);
        this.articleList = res.data.data;
        this.totalList = res.data;
        // setTimeout(() => {
        this.$emit("sendArticleList", this.articleList);
        // }, 1000);
      });
    },

    currentChange(newPageIndex) {
      this.pageIndex = newPageIndex;
      this.start = (this.pageIndex - 1) * this.pageSize;
      this.getList();
    },
    sizeChange(newPageSize) {
      this.pageSize = newPageSize;
      this.getList();
    },

    searchClick() {
      if (this.value == "") {
        this.$message.closeAll();
        this.$message("请输入内容");
      }
      this.$router.push(`/post?city=${this.value}`);
    },

    editorPage() {
      this.$router.push("/post/article");
    },
  },
};
